import { Box, Button } from '@material-ui/core';
import { useStyles } from './styles';

const Cloth = ({key, props}) => {

  const classes = useStyles();

  return (
    <>
      <Box key={key} className={classes.clothCard}>
        <Box style={{ height: 400 }}>
          <img style={{width: 300}} src={process.env.PUBLIC_URL + props.image} alt={props.title} />
          <Box style={{ display: 'flex', alignItems: 'center' }}>
            {
              props.colors.map((item, index) => {
                return (
                  <Box key={index} className={classes.colors} style={{ backgroundColor: item }}></Box>
                );
              })
            }
          </Box>
          <Box style={{ margin: "1px 3px", textAlign: "start", fontWeight: "bold" }}>
            {props.price.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' })}
          </Box>
          <Box style={{ margin: "1px 3px", textAlign: "start" }}>
            {props.title}
          </Box>
          <Box className={classes.description}>
            {props.description}
          </Box>
          <Button className={classes.btn_add}>Adicionar</Button>
        </Box>
      </Box>
    </>
  );
}

export { Cloth }