import { AppBar, Grid, Input, Button, List, Box, Link } from '@material-ui/core';
import { FavoriteBorder, Person, ShoppingBasket } from '@material-ui/icons';

import { useStyles } from './styles';

const Header = () => {

  const classes = useStyles();
  const preventDefault = (event) => event.preventDefault();

  return (
    <>
      <header className={classes.root}>
        <AppBar className={classes.navbar} position="fixed">
          <Box className={classes.promo_tag}>
            <Link href="#" onClick={preventDefault} color="inherit">
              Acompanhe as melhores promoções disponíveis aqui na Maeztra.
            </Link>
          </Box>

          <Grid className={classes.nav_grid} container>
            <Grid className={classes.logo} item xs={2}>
              <img src={`${process.env.PUBLIC_URL + "/assets/logo.png"}`} alt="logo" />
            </Grid>

            <Grid className={classes.searchGroup} item xs={5}>
              <Input className={classes.search} type="text" placeholder="O Que Você Busca?" disableUnderline />
              <Button className={classes.input_button} variant="contained">Buscar</Button>
            </Grid>

            <Grid item xs={4}>
              <List className={classes.navlist}>
                <Button className={classes.btn} startIcon={<Person />}>Minha Conta</Button>

                <Button className={classes.btn} startIcon={<FavoriteBorder />}>Minha Conta</Button>

                <Button className={classes.btn_shop} variant="outlined" startIcon={<ShoppingBasket />}>
                  Meu Carrinho
                </Button>
              </List>
            </Grid>
          </Grid>

        </AppBar>
      </header>
    </>
  );
}

export { Header }
