import { useState } from 'react';
import { Box, List, Link, Fab, Button } from '@material-ui/core';
import { FilterVintage, NavigateBefore, NavigateNext, Public } from '@material-ui/icons';

import { useStyles } from './styles';

const SlideCarroussel = () => {

  const [current, setCurrent] = useState(0);
  const classes = useStyles();
  const filters = ["Vestidos", "Roupas", "Sapatos", "Lingerie", "Acessórios", "OUTLET"];
  const banners = [
    { image: `${process.env.PUBLIC_URL + "/assets/banner.png"}` },
  ];
  const buyHere = [
    { title: "Produtos importados", content: "Produto de Alta Qualidade" },
    { title: "Estoque no Brazil", content: "Produtos mais perto de você!" },
    { title: "Trocas Garantidas", content: "Trocas em até 48 horas, vejas as regras" },
    { title: "Ganhe 5% off", content: "Pagando à vista no Cartão" },
    { title: "Frete Grátis", content: "Em compras acima de R$ 499,00" },
  ]

  const preventDefault = (event) => event.preventDefault();
  const prevSlide = () => {
    setCurrent(current === 0 ? banners.length - 1 : current - 1);
  }
  const nextSlide = () => {
    setCurrent(current === banners.length - 1 ? 0 : current + 1);
  }

  if (!Array.isArray(banners) || banners.length <= 0) {
    return null;
  }

  return (
    <>
      <Box className={classes.root}>
        <List className={classes.list} component="ul">
          <Link className={[classes.novidades, classes.filtersLink]} href="/" onClick={preventDefault} color="inherit">
            <FilterVintage fontSize="small" />
            Novidades
          </Link>
          {filters.map((item, index) => {
            return (
              <Link key={index} className={classes.filtersLink} href="/" onClick={preventDefault} color="inherit">{item}</Link>
            );
          })}
        </List>
      </Box>

      <Box className={classes.slideBox}>
        <Box className={classes.navigateButtons}>
          <Fab color="default">
            <NavigateBefore onClick={prevSlide} fontSize="large" />
          </Fab>
          <Box className={classes.emphasis}>
            <Box className={classes.emphasisContent}>
              <h1>Promoções de Outono</h1>
              <p>
                Confiras os melhores looks para combinar com você nesse Outono
              </p>
              <Button className={classes.conferir} variant="contained">Conferir</Button>
            </Box>
          </Box>
          <Fab color="default">
            <NavigateNext onClick={nextSlide} fontSize="large" />
          </Fab>
        </Box>
        {
          banners.map((item, index) => {
            return (
              <>
                <Box key={index} className={index === current ? 'slide active' : 'slide'}>
                  {index === current && <img className={classes.imgItem} src={item.image} alt="banner" />}
                </Box>
              </>
            );
          })
        }
        <Box className={classes.dotsContainer}>
          {
            Array.from({ length: 5 }).map((_, index) => (
              <Box key={index} className={current === index ? `${classes.dot} ${classes.active}` : `${classes.dot}`}></Box>
            ))
          }
        </Box>
      </Box>

      <Box className={classes.buyHere}>
        <p>Por que comprar na Maeztra?</p>
        <Box className={classes.buyHereTags}>
          {
            buyHere.map((item, index) => {
              return (
                <Box key={index}
                  className={classes.tagItem}
                  style={{ width: 280, height: 80, backgroundColor: "#EFEFEF" }}
                >
                  <Public fontSize="large" />
                  <Box className={classes.itemContent}>
                    <p className={classes.title}>{item.title}</p>
                    <p>{item.content}</p>
                  </Box>
                </Box>
              );
            })
          }
        </Box>
      </Box>
    </>
  );
}

export { SlideCarroussel };
