import { Box, Button, Input, Link, List } from "@material-ui/core";
import { useStyles } from "./styles";
import { medias, payments, logos } from './icons';

const Footer = () => {

  const classes = useStyles();

  return (
    <Box style={{ fontFamily: "Titillium Web", fontStyle: "normal" }}>

      <Box className={classes.signup}>
        <h1 style={{ margin: "0 25px" }}>Receba Nossa Newsletter</h1>
        <Box style={{ margin: "0 25px" }}>
          <Box className={classes.searchGroup} item xs={5}>
            <Input className={classes.search} type="text" placeholder="Digite seu e-mail" disableUnderline />
            <Button className={classes.input_button} variant="contained">Enviar</Button>
          </Box>
        </Box>
      </Box>

      <Box className={classes.info_grid}>
        <Box className={classes.info_card}>
          <List style={{ textAlign: "start" }} component="ul">
            <h3>Informações</h3>
            <p>
              <Link href="/" color="inherit">Quem Somos</Link>
            </p>
            <p>
              <Link href="/" color="inherit">Prazo de Envio</Link>
            </p>
            <p>
              <Link href="/" color="inherit">Trocas e Devoluções</Link>
            </p>
            <p>
              <Link href="/" color="inherit">Promoções e Cupons</Link>
            </p>
          </List>
        </Box>
        <Box className={classes.info_card}>
          <List style={{ textAlign: "start" }} component="ul">
            <h3>Minha Conta</h3>
            <p>
              <Link href="/" color="inherit">Minha Conta</Link>
            </p>
            <p>
              <Link href="/" color="inherit">Meus Pedidos</Link>
            </p>
            <p>
              <Link href="/" color="inherit">Cadastre-se</Link>
            </p>
          </List>
        </Box>
        <Box className={classes.info_card}>
          <List style={{ textAlign: "start" }} component="ul">
            <h3>Onde nos Encontrar</h3>
            <p>
              <Link href="/" color="inherit">Lojas</Link>
            </p>
            <p>
              <Link href="/" color="inherit">Endereço</Link>
            </p>
          </List>
        </Box>
      </Box>

      <Box className={classes.tag_foot}>
        <Box className={classes.tag_item}>
          {
            medias.map((item, index) => {
              return <img key={index} src={item.svg} alt={item.alt} />
            })
          }
        </Box>
        <Box className={classes.tag_item}>
          {
            payments.map((item, index) => {
              return <img key={index} src={item.svg} alt={item.alt} />
            })
          }
        </Box>
        <Box className={classes.tag_item} style={{ color: "#FFFFFF", alignItems: "center" }}>
          <Box style={{ display: "flex", flexDirection: "column" }}>
            <span>Powered by</span>
            <img src={logos.vtex.svg} alt={logos.vtex.alt} />
          </Box>
          <Box style={{ display: "flex", flexDirection: "column" }}>
            <span>Developed by</span>
            <img src={logos.maeztra.svg} alt={logos.maeztra.alt} />
          </Box>
        </Box>
      </Box>
    </Box>
  );
}

export { Footer }
