import { Box } from '@material-ui/core';

const Partner = () => {

  const partners = [
    { image: `${process.env.PUBLIC_URL + "/assets/partners/anntaylor.png"}`, alt: "anntaylor" },
    { image: `${process.env.PUBLIC_URL + "/assets/partners/comma.png"}`, alt: "comma" },
    { image: `${process.env.PUBLIC_URL + "/assets/partners/forever21.png"}`, alt: "forever21" },
    { image: `${process.env.PUBLIC_URL + "/assets/partners/melissa.png"}`, alt: "melissa" },
    { image: `${process.env.PUBLIC_URL + "/assets/partners/zara.png"}`, alt: "zara" },
  ];

  return (
    <>
      <Box style={{ marginTop: 80, height: 140, textAlign: "center" }}>
        <h2>Marcas Parceiras</h2>
        <Box style={{ display: "flex", alignItems: "center", justifyContent: "space-evenly" }}>
          {
            partners.map((item, index) => {
              return (
                <Box key={index} style={{ width: 308, height: 64 }}>
                  <img src={item.image} alt={item.alt} />
                </Box>
              );
            })
          }
        </Box>
      </Box>
    </>
  );
}

export { Partner }
