import { Box } from '@material-ui/core';
import { Cloth } from '../Cloth';
import { mustRequest } from './mustRequest'
import { useStyles } from './styles';

const MustRequest = () => {

  const classes = useStyles();

  return (
    <>
      <Box style={{ marginTop: 70, height: 1200, textAlign: "center" }}>
        <h1>As Mais Pedidas</h1>
        <Box className={classes.must_requests}>
          {
            mustRequest.map((item, index) => {
              return <Cloth key={index} props={item} />
            })
          }
        </Box>
        <Box className={classes.lorem_ipsum}>
          <Box style={{ width: 420, textAlign: "start", fontSize: 14 }}>
            <h1>Lorem ipsum </h1>
            <p>
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Interdum mauris posuere fusce proin mattis.
              Hendrerit cras ut nunc enim dictum. Mattis proin ut quis donec sed eget nulla. Vel mi ut mauris integer.
              Nibh sagittis in lobortis sed cursus condimentum velit pharetra. Amet luctus ut vulputate scelerisque placerat consequat.
              Neque arcu mi iaculis id. Vel vitae, pharetra, a nec tristique. Feugiat id tortor eu mauris pulvinar velit massa.
              Ut ornare augue eget convallis volutpat aliquet. Sed sed pellentesque porttitor phasellus donec condimentum sit sapien.
            </p>
          </Box>
          <Box className={classes.collection}>
            <img style={{ borderRadius: 4 }} src={process.env.PUBLIC_URL + "/assets/nova_colecao.png"} alt="Nova Coleção de Outono" />
          </Box>
        </Box>
      </Box>
    </>
  );
}

export { MustRequest }
