import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    fontFamily: "Titillium Web",
    height: 38,
    marginTop: "6.4rem",
    // border: "1px solid black"
  },
  title: {
    fontWeight: "bold",
  },
  list: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center"
  },
  novidades: {
    color: "orange",
    display: "flex",
    alignItems: "center",
    fontWeight: "bold"
  },
  filtersLink: {
    margin: "0 55px",
    fontFamily: "Titillium Web",
    fontSize: "14px",
  },
  slideBox: {
    position: "relative",
    height: 600,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  navigateButtons: {
    marginTop: -100,
    position: "absolute",
    width: "100%",
    height: 500,
    display: "flex",
    alignItems: "center",
    justifyContent: "space-evenly",
  },
  imgItem: {
    width: "100%",
    height: 600,
    objectFit: "cover"
  },
  dotsContainer: {
    marginTop: 500,
    position: "absolute",
    display: "flex",
    alignItems: "center",
    justifyContent: "space-around",
    width: "160px",
    height: 30,
  },
  dot: {
    width: 15,
    height: 15,
    borderRadius: 25,
    border: "3px solid #FAA500",
  },
  active: {
    backgroundColor: "#FAA500"
  },
  emphasis: {
    width: "60%",
  },
  emphasisContent: {
    width: "35%",
    color: "#FFFFFF",
  },
  conferir: {
    backgroundColor: "#FAA500",
    color: "#FFFFFF",
    fontWeight: "bold"
  },
  buyHere: {
    textAlign: "center",
    fontWeight: "bold"
  },
  buyHereTags: {
    display: "flex",
    alignItems: "center",
    justifyContent: "space-evenly",
    fontWeight: "normal",
  },
  tagItem: {
    display: "flex",
    textAlign: "start",
    alignItems: "center",
    justifyContent: "space-around",

    fontFamily: "Titillium Web",
    fontSize: "14px"
  },
}));